<template>
    <v-card>
        <v-card-title>
            Nutrition
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
        </v-card-title>
        <v-data-table :headers="headers" :items="foods" :search="search"></v-data-table>
    </v-card>
</template>

<script>
export default {
    data() {
        return {
            search: '',
            headers: [
                {
                    text: 'Nama Makanan',
                    align: 'start',
                    sortable: false,
                    value: 'nama_makanan',
                },
                { text: 'Kalori (kcal)', value: 'kalori_kcal' },
                { text: 'Lemak (g)', value: 'lemak_g' },
                { text: 'Karbohidrat (g)', value: 'karbohidrat_g' },
                { text: 'Protein (g)', value: 'protein_g' },
                { text: 'Kalium (mg)', value: 'kalium_mg' },
            ],
            foods: [],
        };
    },
    methods: {
        fetchData() {
            this.axios.get('/api/foods')
                .then((response) => {
                    this.foods = response.data;
                })
                .catch((error) => {
                    console.error(error);
                });
        },
    },
    created() {
        this.fetchData();
    },
};
</script>
